.footer {
 
    width: auto;
    left: 0px;
    right: 0px;
    font-size: 14px;


    padding:40px 0;
    background-image: url("/public/img/mario2.jpg");
    color:#ffffff;
    margin-top: 11rem;
  }
  
  .footer ul {
    padding:0;
    list-style:none;
    text-align:center;
    font-size:20px;
    line-height:1.6;
    margin-bottom:0;
  }
  
  .footer li {
    padding:0 10px;
  }
  
  .footer ul a {
    color:inherit;
    text-decoration:none;
    opacity:0.8;
  }
  
  .footer ul a:hover {
    opacity:1;
  }
  
  .footer .social {
    text-align:center;
    padding-bottom:25px;
  }
  
  .footer .social > a {
    font-size:24px;
    width:40px;
    height:40px;
    line-height:40px;
    display:inline-block;
    text-align:center;
    border-radius:50%;
    border:1px solid #ffffff;
    margin:0 8px;
    color:inherit;
    opacity:0.75;
  }
  
  .footer .social > a:hover {
    opacity:0.9;
  }
  
  .footer .copyright {
    margin-top:15px;
    text-align:center;
    font-size:20px;
    color:#ffffff;
    margin-bottom:0;
  }